@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
*{
  margin:0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

h1{
  margin: 8% 0 15px;
  color: #eee;
}

.App {
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  background-color: #4A5759;
  overflow: auto;
}

.results{
  display: flex;
  width:740px;
  color: #eee;
  justify-content: center;
  flex-wrap: wrap;
  padding: 30px 0;
}

.Form{
  display: flex;
  flex-direction: column;
  width: 740px;
  color: #eee;
}

.Form label{
  margin: 10px 0;
}

.btn{
  align-self: center;
  background-color: #B0C4B1;
  border: none;
  padding: 10px 20px;
  margin-top: 30px;
  border-radius: 5px;
  font-size: 1.2rem;
}

.input{
  border-radius: 5px;
  height: 40px;
  border: none;
  outline: none;
  padding: 0 10px;
  font-size: 1rem;
  background-color:#eee;
}

.dice{
  border: 2px solid #B0C4B1;
  border-radius: 10px;
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-size: 2rem;
}

@media (max-width:750px) {
  .Form{
    width: 90%;
  }
  .results{
    width: 90%;
  }
}
